import { useEffect, useState } from 'react';
import './App.scss';

const DURATION = 100;

const throttle = (function () {
  let timeout: any = undefined;
  return function throttle(callback: any) {
    if (timeout === undefined) {
      callback();
      timeout = setTimeout(() => {
        // allow another call to be throttled
        timeout = undefined;
      }, DURATION);
    }
  }
})();

function throttlify(callback: any) {
  return function throttlified(event: any) {
    throttle(() => {
      callback(event);
    });
  }
}

export const Car = () => {
  const [position, setPosition] = useState(200);
  const [direction, setDirection] = useState("right");

  useEffect(() => {
    const onKeyDown = throttlify((event: any) => {
      keypressHandler(event);
    });

    document.addEventListener("keydown", onKeyDown);

    return function cleanup() {
      document.removeEventListener("keydown", onKeyDown);
    };
  });

  const goLeft = () => {
    setPosition(position - 100);
    setDirection("left");
  }

  const goRight = () => {
    setPosition(position + 100);
    setDirection("right");
  }

  const keypressHandler = (event: any) => {
    if (event.keyCode === 37 ||
      event.keyCode === 65) {
      goLeft();
    }

    if (event.keyCode === 39 ||
      event.keyCode === 68) {
      goRight();
    }
  }

  return (
    <div className="car">
      <img
        className={direction}
        style={{
          left: position
        }}
        src={process.env.PUBLIC_URL + "/car.svg"} />
    </div>
  )
}
