import { useEffect, useState } from 'react';
import './App.scss';

const Column = (props: any) => {
  return (
    <div className={"column " + props.item}><div className="shape" /></div>
  )
}

const Row = (props: any) => {
  return (
    <div className="row">
      {props.item.columns.map((item: any, index: number) => {
        return (
          <Column key={index} item={item} />
        )
      })}
    </div>
  )
}

const getRows = () => {
  const directions = ["bottom-left", "top-left", "top-right", "bottom-right"];
  let rows: any[] = [];

  for (let i = 0; i < 2; i++) {
    const columns: any[] = [];

    for (let y = 0; y < 2; y++) {
      columns.push(directions[Math.floor(directions.length * Math.random())]);
    }

    rows.push({
      columns
    })
  }

  return rows;
}

export const Background = () => {
  const [rows, setRows] = useState(getRows());

  useEffect(() => {
    const interval = setInterval(() => {
      setRows(getRows());
    }, 10000);
    return () => clearInterval(interval);
  }, []);

  return (
    <div className="background">
      <div className="background-image" style={{
        background: "linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(" + process.env.PUBLIC_URL + "/bg.jpg)"
      }} />
      <div className="rows">
        {rows.map((item: any, index: number) => {
          return (
            <Row key={index} item={item} />
          )
        })}
      </div>
    </div>
  )
}
